/*

	State
	0 -   
	1 -  

	PickRate
	        

*/

class Player {
	constructor(props) {
		this.name = props.name;
		this.gender = props.gender;
		this.pickRate = props.pickRate || 0;
		this.score = props.score || 0;
		if (props.streak) {
			this.streak = {
				action: props.streak.action,
				truth: props.streak.truth
			};
		} else {
			this.streak = {
				action: 0,
				truth: 0
			};
		}
	}
}